@import "variables.scss";
@import "mixins.scss";

.#{$class-prefix}wrap {
  margin: 0;
  padding: 0;
  border: 0;
  background: 0;
  width: auto;
  height: auto;
  font-family: inherit;
  font-size: inherit;
  vertical-align: baseline;
  border-radius: 0;
  min-height: 0;
  min-width: 0;
  float: none;
  position: static;
  box-shadow: none;
  text-shadow: none;
  line-height: normal;
  text-indent: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  * {
    margin: 0;
    padding: 0;
    border: 0;
    background: 0;
    width: auto;
    height: auto;
    font-family: inherit;
    font-size: inherit;
    vertical-align: baseline;
    border-radius: 0;
    min-height: 0;
    min-width: 0;
    float: none;
    position: static;
    box-shadow: none;
    text-shadow: none;
    line-height: normal;
    text-indent: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  font-family: Arial,sans-serif;
  font-size: 16px;
  line-height: 1.428571429;
  color: #333333;
}

.#{$class-prefix}overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, .6);
  display: none;
  z-index: 9;
  &.#{$class-prefix}active {
    display: block;
  }
}

.#{$class-prefix}close {
  position: absolute;
  top: 15px;
  right: 15px;
  background: rgba(0, 0, 0, 0.2);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: $gray-base;
  font-size: 20px;
  outline: none;
  line-height: 1px;
  z-index: 9;
}

.#{$class-prefix}round {
  border-radius: 500px;
}

.#{$class-prefix}roundsquare {
  border-radius: 5px;
}

.#{$class-prefix}square {
  border-radius: 0;
}